.footer {
  margin: 0;

  height: 200px;
  background-image: url(../images/footer-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;

  position: sticky;
  bottom: 0;
  z-index: -1;

  @include onTablet {
    height: 480px;
  }

  @include onTablet {
    height: 555px;
  }
}
